<template>
  <div class="orbita">
    <div>
      <md-table>
        <md-table-header>
          <md-table-row>
            <!--<md-table-head v-for="(header) in dataOrbitaHeaders">{{header}}</md-table-head>-->
            <md-table-head v-for="hdr in dataOrbitaHeaders">{{hdr}}</md-table-head>
          </md-table-row>
        </md-table-header>
        
        <md-table-body>
          <md-table-row v-for="rowOrbita in dataOrbita">
            <md-table-cell v-for="cellOrbita in rowOrbita">{{cellOrbita}}</md-table-cell>
          </md-table-row>
        </md-table-body>
      </md-table>
    </div>
  </div>
</template>
<script>
  export default {
    mounted: function () {
      let self = this;
      self.dataOrbitaHeaders = [];
      self.getData();
    },
    name: 'orbita',
    data() {
      return {
        dataOrbita: [],
        dataOrbitaHeaders: []
      }
    },
    methods: {
      getData: function () {
        let options = {
          method: 'get',
          url: '/getDataOrbita',
        };
        this.$http(options)
          .then(
            response => {
              console.log(response);
              try {
                this.dataOrbita = JSON.parse(response.body);
              }
              catch (err) {
                this.dataOrbita = response.body;
              }
              this.dataOrbitaHeaders = [];
              for (let i in this.dataOrbita[0]) {
                this.dataOrbitaHeaders.push(i);
              }
            },
            error => console.warn(error)
          )
          .catch(
            error => console.warn(error)
          );
        
      }
      
      
    },
    
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .orbita{
    height: 99%;
  }
  
  h1, h2 {
    font-weight: normal;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
    text-align: left;
  }
  
  li {
    display: block;
    margin: 0 10px;
    text-align: left;
    
  }
  
  a {
    color: #42b983;
  }
  
  td.text {
    text-align: left;
  }
  
  td.hours {
    text-align: right;
  }
  
  .buttons, .inputs {
    font-size: 24px;
    position: relative;
    width: 100%;
    display: inline-block;
  }
  
  .task, .hours, .url {
    display: inline-block;
    width: 25%;
  }
  
  .hours {
    width: 7%;
  }
  
  .fa-icon {
    width: auto;
    height: 1em; /* or any other relative font sizes */
    
    /* You would have to include the following two lines to make this work in Safari */
    max-width: 100%;
    max-height: 100%;
  }

</style>



